import { useEffect } from 'react';

function NotFound() {
  useEffect(() => {
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: "smooth"
    });
  })

  return (
    <>
      <div style={{height: '90vh'}} className="test">
        <h1>404 - Not Found</h1>
      </div> {/*{temporary space between header and footer}*/}
    </>
  )
}

export default NotFound;